import React, { Component } from 'react';
import Scroll from 'react-scroll';

import TrafficTable from './TrafficTable';
import './App.css';

let Element = Scroll.Element;
let scroller = Scroll.scroller;

let mid = 'm1';


function BtnSearch(props){
  return (
    <div className="tile tile-submit" id='btnSearch' onClick={props.onSubmit} disabled={props.isLoading}>
      {(props.isLoading)? 'Loading...': 'Search'}
    </div>
  );
}

function Demo(props){
  return (
    <div className='demo-tile'>
      <div className="demo-header"></div>
      <span className="goodSpeed demo-block"></span>
      <span className="mediumSpeed demo-block"></span>
      <span className="badSpeed demo-block"></span>
    </div>
  );
}

function Footer(props){
  return (
    <footer>
      <hr/>
      RTSM Project T1117. By <a href='https://royvbtw.uk'>Roy Lu</a>, royvbtw@github #Oct, 2017
    </footer>
  );
}

function MotorwayMenu(props){
  function menuHandler(event){
    mid = event.target.value;
  }

  return (
    <select id='menu-motorways' className='tile block-motorway-menu' onChange={menuHandler}> 
      <option value="m1">1</option>
      <option value='m1e'>1</option>
      <option value='m2'>2</option>
      <option value='m3'>3</option>
      <option value="m3a">3</option>
      <option value="t2f">(2)</option>
      <option value="nar"></option>
      <option value="m4">4</option>
      <option value="m5">5</option>
      <option value="m6">6</option>
      <option value='m8'>8</option>
      <option value="m10">10</option>
      <option value="e62">62</option>
      <option value="e64">64</option>
      <option value="e66">66</option>
      <option value="e68">68</option>
      <option value="e72">72</option>
      <option value="e74">74</option>
      <option value="e76">76</option>
      <option value="e78">78</option>
      <option value="e82">82</option>
      <option value="e84">84</option>
      <option value="e86">86</option>
      <option value="e88">88</option>
    </select>
  );
}


export default class App extends Component {
  constructor(props){
    super(props);

    this.state = ({
      isLoading: false,
      trafficData: null
    });
  }


  setTrafficData = trafficData => {
    this.setState({
      trafficData
    });
  }

  
  /**
   * Do the actual job that query data from the RTSM API service.
   * 
   * This function will use fetch api to query data from the RTSM API service.
   * The response data will save in the App state.
   * @param {number} viewOffset
   */
  doSubmit = ({viewOffset = null} = {}) => {
    if(this.state.isLoading){
      return;
    }
    this.setState({
      isLoading: true
    });
        
    const url = 'https://rtsmapi.royvbtw.uk/data/' + mid;

    fetch(url).then( res => 
      res.json()
    ).then( json => {
      this.setTrafficData(json);
      this.setState({
        isLoading: false
      });

      // scroll view to table or specified position
      const scrollerOpts = {
        duration: 800,
        delay: 100,
        smooth: true
      };
      if(viewOffset){
        //scroller.scrollTo(viewOffset, scrollerOpts);
      }else{
        scroller.scrollTo('tableAnchor', scrollerOpts);
      }
    });
  }

  
  /**
   * Refresh page with cached mid.
   */
  refreshPage = () => {
    const viewOffset = window.scrollY;
    this.doSubmit({viewOffset});
  }
  

  // A wrapper function for doSubmit()
  shortcut = argMid => {
    if(argMid){
      mid = argMid;
      this.doSubmit();
    }
  }
  
  render() {
    return (
      <div className="App">
        {(this.state.isLoading) && (<div id='spinner'></div>)}
        
        <div className="menu-container">
          <div className="main-block">
            <div className='tile app-title'>RTSM</div>
            <div className="tile block-motorway-label"></div>
            <MotorwayMenu />
            <BtnSearch isLoading={this.state.isLoading} onSubmit={this.doSubmit} />
          </div>

          <div className='shortcut-block'>
            <div className="tile-shortcut bg-pink" onClick={e => this.shortcut('m1')}>
              M1<br/>1
            </div>
            <div className="tile-shortcut bg-darkpink" onClick={e => this.shortcut('m3')}>
              M3<br/>3
            </div>
            <div className="tile-shortcut bg-orange" onClick={e => this.shortcut('m5')}>
              M5<br/>5
            </div>
            <Demo />
          </div>
        </div>

        <Element name='tableAnchor'></Element>
        <TrafficTable data={this.state.trafficData} />
        <div id='btnRefresh' onClick={this.refreshPage}>
          {(this.state.isLoading)? 'loading': 'Refresh'}
        </div>
        <Footer />
      </div>
    );
  }
}



